// Component em React
import React from "react";
import "./styles.css"; // Importa o arquivo de estilo CSS
import { useNavigate } from "react-router-dom"; // Para navegação entre rotas

const ShowPrivacidade = () => {
    const navigate = useNavigate();

    return (
        <div style={{ backgroundColor: 'transparent', marginTop:2, marginBottom:-10, justifySelf:"center"}}>
            <button onClick={() => navigate('/politicaprivacidade')} className="btn">
                Política de Privacidade
            </button>
        </div>
    );
};

export default ShowPrivacidade;
