import React, { useState, useEffect } from "react";
import { BrowserRouter as Router, Routes, Route, NavLink, Navigate } from "react-router-dom";
import HomeScreen from "./screens/HomeScreen";
import PaginaLotoFacil from "./screens/PaginaLotoFacil";
import PaginaLotoMania from "./screens/PaginaLotoMania";
import PaginaMegaSena from "./screens/PaginaMegaSena";
import PaginaPoliticaPrivacidade from "./screens/PaginaPoliticaPrivacidade";
import PaginaSobre from "./screens/PaginaSobre";
import usePageTracking from "./usePageTracking";

import "./styles.css"; // CSS global para estilos comuns

const App = () => {
  console.log("App está dentro do Router");


  const [menuOpen, setMenuOpen] = useState(false);

  const toggleMenu = () => {
    setMenuOpen((prev) => !prev);
  };

  const closeMenu = () => {
    setMenuOpen(false);
  };

  // Ativa o rastreamento de páginas
  usePageTracking();

  useEffect(() => {
    // Logando o estado inicial do dataLayer para debug
    if (window.dataLayer) {
      console.log("DataLayer inicial:", window.dataLayer);
    }
  }, []);


  return (
    <Router basename="/"> {/* Definindo o basename como "/" */}
      <div className="app">
        {/* Logo acima do menu */}
        <div className="menu-logo-container">
          <img src="/logo.webp" alt="Logo" className="logo-image" />
        </div>

        {/* Botão do Menu Hamburger */}
        <button className="menu-toggle" onClick={toggleMenu}>
          ☰
        </button>

        {/* Menu de Navegação */}
        <nav className={`menu ${menuOpen ? "open" : ""}`}>
          <NavLink to="/" className="menu-item inicio" end onClick={closeMenu}>
            Início
          </NavLink>
          <NavLink
            to="/lotofacil"
            className="menu-item lotofacil"
            onClick={closeMenu}
          >
            LotoFácil
          </NavLink>
          <NavLink
            to="/megasena"
            className="menu-item megasena"
            onClick={closeMenu}
          >
            Mega-Sena
          </NavLink>
          <NavLink
            to="/lotomania"
            className="menu-item lotomania"
            onClick={closeMenu}
          >
            LotoMania
          </NavLink>
          <NavLink
            to="/sobre"
            className="menu-item sobre"
            onClick={closeMenu}
          >
            Sobre
          </NavLink>
        </nav>

        {/* Overlay para fechar o menu ao clicar fora */}
        {menuOpen && <div className="overlay" onClick={closeMenu}></div>}
      </div>

      {/* Conteúdo */}
      <div className="content">
        <Routes>
          <Route path="/" element={<HomeScreen />} />
          <Route path="/sobre" element={<PaginaSobre />} />
          <Route path="/politicaprivacidade" element={<PaginaPoliticaPrivacidade />} />
          <Route path="/lotofacil" element={<PaginaLotoFacil />} />
          <Route path="/lotomania" element={<PaginaLotoMania />} />
          <Route path="/megasena" element={<PaginaMegaSena />} />
          <Route path="*" element={<Navigate to="/" />} />
        </Routes>
      </div>
    </Router>
  );
};

export default App;
