import React, { useEffect } from 'react';

const AdBanner = () => {

  useEffect(() => {
    // Criando o iframe
    const iframe = document.createElement('iframe');
    iframe.src = "https://ad.lomadee.com/banner/view?sourceId=38327723&dimension=9&width=350&height=350&method=0&advertisers=&tags=";
    iframe.style = 'width: 350px; height: 350px; border: none; margin: 0; padding: 0;';
    iframe.frameBorder = 0;
    iframe.width = "350";
    iframe.height = "350";
    iframe.allowTransparency = true;
    iframe.scrolling = 'no';
    iframe.marginheight = '0';
    iframe.marginwidth = '0';

    // Função de carregamento com sucesso
    iframe.onload = () => {
      console.log('Banner carregado com sucesso!');
    };

 
    // Adicionando o iframe ao DOM
    const bannerContainer = document.getElementById('ad-banner-container');
    if (bannerContainer) {
      bannerContainer.appendChild(iframe);
    }

    // Cleanup ao desmontar o componente
    return () => {
      if (bannerContainer) {
        bannerContainer.removeChild(iframe);
      }
    };
  }, []);

  return (
    <div id="ad-banner-container" />
   
  );
};

export default AdBanner;
