import React, { createContext, useState } from "react";
import { toast } from 'react-toastify';

export const ContextMegaSena = createContext();

export const MegaSenaProvider = ({ children }) => {
  const [n] = useState(6);
  const [numbers, setNumbers] = useState([]);
  const [loading, setLoading] = useState(false);
  const [showSave, setShowSave] = useState(true);
  const [showButaoLimpar, setShowButaoLimpar] = useState(true)
  const [showAguardandoPalpites, setShowAguardandoPalpites] = useState(true)

  const [data, setData] = useState(() => {
    // Inicializar com dados salvos no localStorage para MegaSena
    const savedData = localStorage.getItem("megaSenaNumbers");
    return savedData ? JSON.parse(savedData) : [];
  });

  let clickCount = 0;

  const generateNumbersMegaSena = () => {
    clickCount += 1; // Incrementa a contagem a cada chamada
    console.log(`Clique número: ${clickCount}`); // Log do número de cliques
  
    if (clickCount === 3) {
      console.log("Abrindo anúncio intersticial");
      const interstitialScript = document.createElement('script');
      interstitialScript.src = 'https://tags.orquideassp.com/tag/91421'; // Certifique-se de usar HTTPS
      interstitialScript.async = true; // Garante que o script não bloqueie a execução do restante do código
      interstitialScript.onload = () => console.log("Script intersticial carregado com sucesso!");
      interstitialScript.onerror = () => console.error("Falha ao carregar o script intersticial.");
      document.body.appendChild(interstitialScript);
      clickCount = 0; // Reinicia o contador
    }
    
  
    
    setLoading(true);

    setTimeout(() => {
      setLoading(false);
      setShowSave(false);

      const randomNumbers = new Set();
      while (randomNumbers.size < n) {
        randomNumbers.add(Math.floor(Math.random() * 60) + 1); // Gera números entre 1 e 60
      }

      const sortedNumbers = [...randomNumbers].sort((a, b) => a - b);
      setNumbers(sortedNumbers);
    }, 2000);
  };

  const saveData = () => {
    setShowButaoLimpar(false);
    setShowAguardandoPalpites(false);
    const updatedData = [...data, numbers];
    setData(updatedData);
    localStorage.setItem("megaSenaNumbers", JSON.stringify(updatedData));

    toast.success("Números salvos com sucesso!", {
      position: "top-right", // Use string ao invés de `toast.POSITION.TOP_RIGHT`
      autoClose: 3000, // 3 segundos
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      draggable: true,
      progress: undefined,
    });
  };

  const readData = () => {
    const savedData = localStorage.getItem("megaSenaNumbers");
    if (savedData) {
      setData(JSON.parse(savedData));
    } else {
      alert("Nenhum dado salvo.");
    }
  };

  const deleteData = () => {
    setShowButaoLimpar(true);
    setShowAguardandoPalpites(true);
    const confirmed = window.confirm("Tem certeza que deseja apagar todos os Palpites da Sorte?");
    if (confirmed) {
      localStorage.removeItem("megaSenaNumbers"); // Remove os palpites
      setData([]);
      toast.success("Palpites apagados com sucesso!", {
        position: "top-right",
        autoClose: 3000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        draggable: true,
        style: {
          backgroundColor: "black",
          color: "#ffffff",
        },
      });
    }
  };

  const copiarPalpites = () => {
      // Verifica se há números salvos no localStorage
      const savedData = localStorage.getItem("megaSenaNumbers"); // Corrigido para a chave "lotoManiaNumbers"
      if (!savedData || savedData === "[]") {
        toast.error("Nenhum palpite salvo para copiar.", {
          position: "top-right",
          autoClose: 3000,
          hideProgressBar: false,
          closeOnClick: true,
          pauseOnHover: true,
          draggable: true,
        });
        return;
      }
    
      const parsedData = JSON.parse(savedData); // Converte os dados do localStorage para objeto
      const textToCopy = parsedData.map((numbers) => numbers.join(", ")).join("\n"); // Cria uma string com os números, separados por vírgulas e cada conjunto de números em uma nova linha
    
      if (navigator.clipboard && navigator.clipboard.writeText) {
        navigator.clipboard.writeText(textToCopy)
          .then(() => {
            toast.success("Palpites copiados para a área de transferência!", {
              position: "top-right",
              autoClose: 3000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
            });
          })
          .catch((err) => {
            toast.error("Erro ao copiar palpites. Tente novamente.", {
              position: "top-right",
              autoClose: 3000,
              hideProgressBar: false,
              closeOnClick: true,
              pauseOnHover: true,
              draggable: true,
            });
            console.error("Erro ao copiar para a área de transferência: ", err);
          });
      } else {
        // Método alternativo para navegadores sem suporte à API Clipboard
        try {
          const textarea = document.createElement("textarea");
          textarea.value = textToCopy;
          document.body.appendChild(textarea);
          textarea.select();
          document.execCommand("copy");
          document.body.removeChild(textarea);
    
          toast.success("Palpites copiados para a área de transferência!", {
            position: "top-right",
            autoClose: 3000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
          });
        } catch (err) {
          toast.error("Erro ao copiar palpites. Tente novamente.", {
            position: "top-right",
            autoClose: 3000,
            hideProgressBar: false,
            closeOnClick: true,
            pauseOnHover: true,
            draggable: true,
          });
          console.error("Erro ao copiar usando método alternativo: ", err);
        }
      }
    };
    

  return (
    <ContextMegaSena.Provider
      value={{
        data,
        numbers,
        loading,
        showSave,
        showButaoLimpar,
        showAguardandoPalpites,
        saveData,
        readData,
        deleteData,
        copiarPalpites,
        setShowButaoLimpar,
        generateNumbersMegaSena,
        setShowAguardandoPalpites,
      }}
    >
      {children}
    </ContextMegaSena.Provider>
  );
};
