/*Tela Palpites Salvos LotoFácil */
import React, { useContext, useEffect } from 'react';
import { ContextLotoFacil } from '../../context/ContextLotoFacil';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import styles from './LotoFacil.module.css';
import AdBanner from "../AdBanner"

const PalpitesSalvos = () => {
  const { data, deleteData, showButaoLimpar, showAguardandoPalpites, setShowButaoLimpar,setShowAguardandoPalpites } = useContext(ContextLotoFacil);

  useEffect(() => {
    if (data.length > 0) {
      setShowButaoLimpar(false);
      setShowAguardandoPalpites(false);
    } else {
      setShowButaoLimpar(true);
      setShowAguardandoPalpites(true);
    }
  }, [data, setShowButaoLimpar, setShowAguardandoPalpites]);


  useEffect(() => {
    if (data.length > 0) {
      setShowButaoLimpar(false);
      setShowAguardandoPalpites(false);
    } else {
      setShowButaoLimpar(true);
      setShowAguardandoPalpites(true);
    }
  }, [data, setShowButaoLimpar, setShowAguardandoPalpites]);


  const renderItem = (item, index) => (
    <div className={styles.content}>
      <div>
        <p className={styles.text}>Palpite {index + 1}</p>
      </div>

      <div className={styles.numberList}>
        {item.map((number, numberIndex) => (
          <div className={styles.numberCircle} key={numberIndex}>
            <span className="numberText">{number}</span>
          </div>
        ))}
      </div>

     {/*  <button className={styles.copyBtn} onClick={() => copiarPalpites(item)}>
        <i className="material-icons">copiar</i>
      </button>
      */}
    </div>
  );

  return (
    
    <div className="container PalpiteSalvos" style={{ display:"flex", flex:1,  height:"auto", marginBottom:"40%",boxSizing:"border-box"}}>
    {!showAguardandoPalpites ?
      <div className={styles.listContainer}>
        {data.map((item, index) => (
          <div key={index}>
            {renderItem(item, index)}
            {/*  <hr className="line" />*/}
          </div>
        ))}
      </div>
      :
      <div className={styles.aguardandoPalpites}>
        <p className={styles.text}>
          Seus palpites salvos da MegaSena irão aparecer aqui!
        </p>
      </div>
    }
    {showButaoLimpar ?
      <></> :
      <button className="button limpar" onClick={deleteData}>
        <span className="btnTxt">Apagar palpites</span>
      </button>}
      <div style={{ height: "20px" }} className="banner">
                <AdBanner/>
            </div>
    <ToastContainer />
  </div>
  );
};

export default PalpitesSalvos;
