// src/components/Ad/AnchorAd.js
import React, { useEffect, useState } from "react";

const AnchorAd = () => {
    const [adScriptLoaded, setAdScriptLoaded] = useState(false);
    const [adScriptError, setAdScriptError] = useState(false);

    useEffect(() => {
        // Adicionando o script de anúncio dinamicamente
        const script = document.createElement('script');
        script.src = '//tags.orquideassp.com/tag/91417';
        script.async = true;
        script.onload = () => {
            setAdScriptLoaded(true);
        };
        script.onerror = () => {
            console.error('Erro ao carregar o script do anúncio');
            setAdScriptError(true);
        };
        document.body.appendChild(script);

        return () => {
            // Limpar o script ao desmontar o componente
            document.body.removeChild(script);
        };
    }, []);

    if (adScriptError) {
        return <div>Erro ao carregar o anúncio. Tente novamente mais tarde.</div>;
    }

    if (!adScriptLoaded) {
        return <div>Carregando anúncio...</div>;
    }

    return (
        <div className="anchorAdContainer">
            <ins className="adsbyorquideassp"
                 data-ad-client="ca-pub-XXXXXXXXXXXX"
                 data-ad-slot="XXXXXXXXXXXX"></ins>
        </div>
    );
};

export default AnchorAd;
