import React, { useContext } from "react";
import "../../styles.css";
import styles from './LotoMania.module.css';
import { AppContext } from "../../context/AppContext";
import Footer from "../../components/Footer";
import GerarPalpites from "../../components/LotoMania/GerarPalpites";
import PalpitesSalvos from "../../components/LotoMania/PalpitesSalvos";
import Resultado from "../../components/LotoMania/Resultado";
import ListaParceiros from "../../components/Parceiros/ListaParceiros";
import ComoJogarLotoMania from "../../components/LotoMania/ComoJogarLotoMania";

const PaginaLotoMania = () => {
  const {
    salvos,
    resultados,
    mostrarSalvos,
    mostrarResultados,
    mostrarGerarPalpites
  } = useContext(AppContext);

  return (
    <div className="container">
      <div className="column left"><ComoJogarLotoMania /></div>
      <div className="column center">
        <h1 className={styles.title}>Palpites LotoMania</h1>
        <nav className={styles.menu}>
          <button className={`${styles.menuButton} ${styles.secondary}`} onClick={mostrarGerarPalpites}>
            Gerar Palpites
          </button>
          <button className={`${styles.menuButton} ${styles.primary}`} onClick={mostrarSalvos}>
            Palpites Salvos
          </button>
          <button className={`${styles.menuButton} ${styles.secondary}`} onClick={mostrarResultados}>
            Resultados
          </button>
        </nav>
        {!resultados && !salvos ? (
          <GerarPalpites />
        ) : salvos ? (
          <PalpitesSalvos />
        ) : (
          <Resultado />
        )}
      </div>
      <div className="column right">
        <ListaParceiros />
      </div>
      <Footer />

      {/* Script do Anúncio */}
      <div dangerouslySetInnerHTML={{
        __html: `<script async src='//tags.orquideassp.com/tag/91417'></script>`
      }} />
    </div>
  );
};

export default PaginaLotoMania;
