import React from "react";

const ListaNoticias = () => {
  const noticias = [
    {
      "Nome": "LotoFácil premiou 3 apostas",
      "Link": "https://einvestidor.estadao.com.br/loterias/lotofacil-resultado-concurso-3286-premio/",
      imgSrc: require("../../images/noticias/lotofacil1.webp"),
      "Texto": "As apostas vencedoras são das seguintes cidades: Anage (BA), Caarapo (MS) e Itaquaquecetuba (SP). Números sorteadaos: 01-04-05-06-07-08-11-13-14-18-20-21-23-24-25."
    },
    {
      "Nome": "Mega da Virada 2024",
      "Link": "https://bnldata.com.br/",
      imgSrc: require("../../images/noticias/megavirada.webp"),
      "Texto": "A Mega da Virada de 2024, com prêmio de R$ 635 milhões, emocionou os brasileiros. O sorteio de 31 de dezembro contou com números como 01, 17, e 50. Milhões de apostas foram feitas para tentar conquistar o prêmio milionário."
    },
    {
      "Nome": "Prêmios Não Resgatados",
      "Link": "https://g1.globo.com/loterias/noticia/2024/12/31/brasileiros-deixaram-de-sacar-mais-de-r-380-milhoes-em-premios-de-loterias-em-2024-diz-caixa.ghtml",
      imgSrc: require("../../images/noticias/megaresultdo.webp"),
      "Texto": "Em 2024, mais de R$ 380 milhões em prêmios de loterias não foram resgatados. Esse montante será destinado ao Fundo de Financiamento Estudantil (Fies). A quantidade de prêmios não retirados apresentou uma leve queda em comparação ao ano anterior."
    },
    {
      "Nome": "Mega-Sena Acumulada",
      "Link": "https://g1.globo.com/loterias/",
      imgSrc: require("../../images/noticias/mega3.webp"),
      "Texto": "O sorteio 2.811 da Mega-Sena, realizado em dezembro, terminou sem ganhadores. O prêmio acumulou em R$ 8,5 milhões e a expectativa para o próximo concurso é grande. Apostadores esperam que o prêmio seja finalmente conquistado em breve."
    },
    {
      "Nome": "Resultado da Lotofácil",
      "Link": "https://g1.globo.com/loterias/",
      imgSrc: require("../../images/noticias/loto.webp"),
      "Texto": "O concurso 3285 da Lotofácil premiou quatro apostas vencedoras. Cada uma delas levará uma parte do prêmio, estimado em R$ 1,7 milhão. A Lotofácil continua sendo uma das preferidas pelos apostadores brasileiros."
    },
    {
      "Nome": "Prêmio da Timemania Acumulado",
      "Link": "https://noticias.uol.com.br/loterias/ultimas/",
      imgSrc: require("../../images/noticias/time.webp"),
      "Texto": "A Timemania acumulou e o prêmio já chega a R$ 2 milhões. Nenhum apostador conseguiu acertar todos os números, e agora a expectativa para o próximo sorteio é ainda maior. A competição pelo prêmio tem atraído cada vez mais participantes."
    }
  ];

  return (
    <div
      style={{
        width: "90%",
        maxWidth: "600px", // Limita a largura máxima
        margin: "0px auto", // Centraliza o componente horizontalmente
        padding: "10px",
        backgroundColor: "#f9f9f9",
        borderRadius: "10px",
        boxShadow: "0 4px 8px rgba(0, 0, 0, 0.1)",
        maxHeight: "860px", // Limita a altura do contêiner
        overflowY: "auto", // Habilita o scroll quando o conteúdo exceder a altura

      }}
    >
      <p
        style={{
          fontWeight: "bold",
          fontSize: "1.2rem",
          color: "#333",
          marginBottom: "20px",
          textAlign: "center"
        }}
      >
        Últimas Notícias sobre Loterias no Brasil:
      </p>

      {noticias.map((noticia, index) => (
        <div
          key={index}
          style={{
            display: "flex",
            flexDirection: "column",
            alignItems: "center",
            marginBottom: "20px" // Espaçamento consistente entre os itens
          }}
        >
          <a href={noticia.Link} target="_blank" rel="noopener noreferrer">
            <img
              src={noticia.imgSrc}
              alt={noticia.Nome}
              style={{
                width: '260px',
                height: '120px', // Proporção mantida
                objectFit: "cover",
                borderRadius: "10px",
                marginBottom: "10px", // Espaço reduzido entre a imagem e o texto
                justifySelf:"center",
                alignSelf:"center",
              }}
            />
          </a>
          <p
            style={{
              fontWeight: "bold",
              color: "#222",
              textAlign: "center", // Centraliza o nome
              margin: "10px 0 5px" // Espaçamento entre nome e texto
            }}
          >
            {noticia.Nome}
          </p>
          <p
            style={{
              color: "#555",
              lineHeight: "1.6",
              textAlign: "justify", // Justificado
              margin: 0, // Remove margens adicionais
              padding: "0 10px" // Pequeno padding lateral para melhorar leitura
            }}
          >
            {noticia.Texto}
          </p>
        </div>
      ))}
    </div>
  );
};

export default ListaNoticias;
