import React from 'react';

const ListaParceriros = () => {
  const list = [
    {
      "Nome": "NossaBet",
      "Link": "https://acesse.vc/v2/4d24b63cc",
      "Imagem": "https://cms-rj.nossabet.com.br/media/images/newtvbanners/1298.jpeg?v=2",
      "Texto": "NossaBet é um site de apostas esportivas e jogos online que preza pela experiência dos apaixonados por entretenimento. Aqui você encontra uma grande variedade de mercados, esportes e jogos, em um ambiente seguro e projetado para clientes de todo o Brasil."
    },
    {
      "Nome": "MarsBet",
      "Link": "https://acesse.vc/v2/497215304",
      "Imagem": "https://public-content.prod.wisegaming.com/Assets/marsbet-br/PT-50%20WELCOME%20BONUSPromo.webp_1731179640055",
      "Texto": "Marsbet é a plataforma ideal para quem busca emoção e grandes oportunidades de apostas online."
    }
  ];

  return (
    <div style={{
      height:"100%",
      maxWidth: "800px", // Limita a largura máxima
      padding: "20px",
      backgroundColor: "#f9f9f9",
      borderRadius: "10px",
      maxHeight: "560px", // Limita a altura do contêiner
      overflowY: "auto" // Habilita o scroll quando o conteúdo exceder a altura
    }}>
      <p style={{
        fontSize: "1.2rem",
        marginTop:"10px",
        fontWeight: 'bold',
        color: '#333',
        marginBottom: '30px',
        textAlign: 'center',
      }}>
        Confira abaixo alguns dos nossos parceiros:
      </p>

      {list.map((item, index) => (
        <div key={index} style={{
          display: 'flex',
          flexDirection: 'column',
          alignItems: 'center',
        }}>
          <a href={item.Link} target="_blank" rel="noopener noreferrer">
            <img
              src={item.Imagem}
              alt={item.Nome}
              style={{
                width: '100%',
                maxWidth: '300px', // Tamanho máximo da imagem
                height: 'auto', // Proporção mantida
                objectFit: 'contain',
                borderRadius: '10px',
              }}
            />
          </a>
          <p style={{
            fontWeight: 'bold',
            color: '#222',
            textAlign: 'center', // Centraliza o nome
            margin: '10px 0 5px', // Espaçamento entre nome e texto
          }}>
            {item.Nome}
          </p>
          <p style={{
            color: '#555',
            lineHeight: '1.6',
            textAlign: 'justify', // Justificado
            margin: 0, // Remove margens adicionais
            padding: '0 10px', // Pequeno padding lateral para melhorar leitura
          }}>
            {item.Texto}
          </p>
        </div>
      ))}
    </div>
  );
};

export default ListaParceriros;
