// AdBanner.js
import React, { useEffect } from 'react';

const Rectangle = () => {
    useEffect(() => {
        const adScript = document.createElement('script');
        adScript.src = '//tags.orquideassp.com/tag/91427'; // Código do anúncio retangular
        adScript.async = true;
        document.body.appendChild(adScript);

        return () => {
            document.body.removeChild(adScript); // Limpa o script ao desmontar
        };
    }, []);

    return (
        <div id="ad-banner">
        </div>
    );
};

export default Rectangle;