// FluidAd.js
import React, { useEffect } from 'react';

const FluidAd = () => {
    useEffect(() => {
        const adScript = document.createElement('script');
        adScript.src = '//tags.orquideassp.com/tag/91419'; // Código do anúncio fluid
        adScript.async = true;
        document.body.appendChild(adScript);

        return () => {
            document.body.removeChild(adScript); // Limpa o script ao desmontar
        };
    }, []);

    return (
        <div id="fluid-ad">
            {/* Elemento onde o anúncio será renderizado */}
        </div>
    );
};

export default FluidAd;