import React from "react";
import { useNavigate } from "react-router-dom";
import ShowPrivacidade from "../../components/ShowPrivacidade";
import CookieConsent from "../../components/CookieConsent";
import Footer from "../../components/Footer";
import ListaParceriros from "../../components/Parceiros/ListaParceiros";
import ListaNoticias from "../../components/Noticias/ListaNoticias";
import FluidAd from "../../components/Anuncios/FluidAd";
const HomeScreen = () => {
  const navigate = useNavigate();

  const loterias = [
    {
      name: "LotoFácil",
      path: "/lotofacil",
      imgSrc: require("../../images/lotofacil.webp"), // Caminho relativo a partir deste arquivo
    },
    {
      name: "LotoMania",
      path: "/lotomania",
      imgSrc: require("../../images/lotomania.webp"), // Caminho relativo
    },
    {
      name: "MegaSena",
      path: "/megasena",
      imgSrc: require("../../images/megasena.webp"), // Caminho relativo
    },
  ];

  return (
    <div className="container">
      <div className="column left noticiais"><ListaNoticias /></div>
      <div className="column center">
        <ShowPrivacidade />
        <div className="container HomeScreen" style={{}}>
          <div
            className="scrollable-content"
            style={{
              flex: "1",
              overflowY: "auto",
              maxHeight: "580px",
              padding: "10px",
              borderRadius: "5px",
              boxShadow: "0 4px 6px rgba(0, 0, 0, 0.1)",
            }}
          >
            {loterias.map((loteria, index) => (
              <div
                key={index}
                onClick={() => navigate(loteria.path)} // Redireciona ao clicar
                style={{
                  background: "white",
                  border: "1px solid #ccc",
                  borderRadius: "10px",
                  boxShadow: "0 4px 6px rgba(0, 0, 0, 0.1)",
                  padding: "4px",
                  margin: "10px auto",
                  maxWidth: "320px",
                  transition: "transform 0.3s ease, box-shadow 0.3s ease",
                  cursor: "pointer",
                }}
              >
                <img
                  src={loteria.imgSrc}
                  alt={loteria.name}
                  style={{ maxWidth: "100%", borderRadius: "10px" }}
                />
                <h2 style={{ fontSize: "15px", margin: "10px 0", color: "#666", marginTop: 10, marginLeft: 10, justifySelf: "flex-start" }}>Palpites da Sorte {loteria.name}</h2>
                <p style={{ fontSize: "14px", color: "#666", textAlign: "left", marginLeft: 10 }}>
                  {`${loteria.name === "LotoFácil"
                    ? "Deixe a sorte trabalhar a seu favor com nossos palpites otimizados para LotoFácil."
                    : loteria.name === "LotoMania"
                      ? "Experimente a emoção de criar combinações vencedoras na LotoMania."
                      : "Aposte nos números da sorte e dispute os maiores prêmios do Brasil."
                    }`}
                </p>
              </div>
            ))}
            <div style={{ height: "0px" }} className="banner">
              <FluidAd />
            </div>
          </div>
        </div>
        <CookieConsent />
      </div>

      <div className="column right"><ListaParceriros /></div>
      <Footer />
    </div >
  );
};

export default HomeScreen;
